<script>
	export default {
		data() {
			return {
				Request: this.$api.api.prototype, //请求头
			}
		},
		methods: {
			
		},
		onShow() {
			console.log('hha ') 
			wx.getSystemInfo({
				success(res) {
					console.log(res.model)
					console.log(res.pixelRatio)
					console.log(res.windowWidth)
					console.log(res.windowHeight)
					console.log(res.language)
					console.log(res.version)
					console.log(res.platform)
				}
			})
		},

	}
</script>

<style>
	input {
		border-radius: 0rem;
	}
	
	.divHide {
		display: none !important;
	}
	
	.div_float:after {
		clear: both;
		content: '';
		width: 0;
		height: 0;
		visibility: hidden;
		display: block;
	}
	/*文字限制超出一行后变省略号*/
	
	.eli_one {
		overflow: hidden;
		text-overflow: ellipsis;
		display: box;
		display: -webkit-box !important;
		-webkit-line-clamp: 1;
		-webkit-box-orient: vertical;
		white-space: pre-line;
	}
	/*文字限制超出两行行后变省略号*/
	
	.eli_two {
		overflow: hidden;
		text-overflow: ellipsis;
		display: box;
		display: -webkit-box !important;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		white-space: pre-line;
	}
	/*文字限制超出两行行后变省略号*/
	
	.eli_four {
		overflow: hidden;
		text-overflow: ellipsis;
		display: box;
		display: -webkit-box !important;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;
		white-space: pre-line;
	}
	/*固定页面*/
	
	.fixedView {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
	/*搜索框*/
	
	.seaView span {
		width: 30rpx;
		height: 30rpx;
		display: inline-block;
		vertical-align: middle;
		margin: 0 20rpx;
		margin-top: -5rpx;
	}
	
	.seaView {
		width: 95%;
		height: 64rpx;
		line-height: 64rpx;
		background-color: #f2f2f2;
		border-radius: 8rpx;
		color: #999999;
		font-size: 28rpx;
		margin: 0 auto;
	}
	/*placehodel颜色*/
	
	.placeClass {
		color: #999999;
	}
	/*搜索Icon*/
	
	.seaIcon {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*收藏白色*/
	
	.coll_w {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*右箭头 灰色*/
	
	.arr_g {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*单选框未选中*/
	
	.no_sel {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*单选框  已选中*/
	
	.is_sel {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*相机*/
	
	.cameraIcon {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*问号  */
	
	.issueIcon {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*复选框  选中*/
	
	.cb_sel {
		background: url('data:image/png;base64,') !important;
		background-size: 100% 100% !important;
	}
	/*复选框  未选中*/
	
	.cb_no {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*打钩单选*/
	
	.gouSel {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*倒三角*/
	
	.downIcon {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*打钩白色*/
	
	.gouIcon {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
	/*地址icon*/
	
	.adrIcon {
		background: url('data:image/png;base64,');
		background-size: 100% 100%;
	}
</style>


